import firtSlide from '../img/slide1.jpg';
import secondSlide from '../img/slide2.png';
import thirdSlide from '../img/slide3.jpg';

import SliderHeader from './SliderHeader';

export default function Slider() {
    return (
        <div className="slider mr-2 ml-2">
            <div id="myCarousel" className="carousel slide" data-bs-ride="carousel">
                <div className="carousel-indicators">
                    <button type="button" data-bs-target="#myCarousel" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1"></button>
                    <button type="button" data-bs-target="#myCarousel" data-bs-slide-to="1" aria-label="Slide 2"></button>
                    <button type="button" data-bs-target="#myCarousel" data-bs-slide-to="2" aria-label="Slide 3"></button>
                </div>
                <div className="carousel-inner">
                    <div className="carousel-item active">
                        <img className="bd-placeholder-img"  src={firtSlide} width="100%"/>
                        <SliderHeader />
                    </div>
                    <div className="carousel-item">
                        <img className="bd-placeholder-img"  src={secondSlide} width="100%"/>
                        <SliderHeader />
                    </div>
                    <div className="carousel-item">
                        <img className="bd-placeholder-img"  src={thirdSlide} width="100%"/>
                        <SliderHeader />
                    </div>
                </div>

                <button className="carousel-control-prev" type="button" data-bs-target="#myCarousel" data-bs-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="visually-hidden">Previous</span>
                </button>
                <button className="carousel-control-next" type="button" data-bs-target="#myCarousel" data-bs-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="visually-hidden">Next</span>
                </button>
            </div>
        </div>
    );
}